body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}


.title{
    margin-top:30px;
}


.card{
    width: 12rem;
    margin-top:3em;
    margin-left: 2em;
    box-shadow: 10px 10px 40px -12px rgba(0, 0, 0, 0.75);
}

@media (min-width: 324px)and (max-width: 767px) { 
    .card{
        position: relative;
        left: 3.2em;
    }    
  }

.tarjeta{
    box-shadow: 10px 10px 40px -12px rgba(0, 0, 0, 0.75);
}
.card:hover{
    -webkit-transform:scale(1.2);transform:scale(1.2);
}

.card-header{
    color:#fff;
    background-color: #FF5A49!important;
    font-weight: 600;
}

.padre{
    display:flex;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 3em;
}

.inputPokemon{
    margin-top:2em;
}

.has-search .form-control {
   padding-left: 2.375rem;
}

.has-search .form-control-feedback {
    position: absolute;
    text-align: center;
    z-index: 2;
    display: block;
    width: 2.375rem;
    height: 2.375rem;
    line-height: 2.375rem;
    pointer-events: none;
    color: #FF5A49;
}

a:hover{
    text-decoration: none!important;
}

.btn-danger{
    background-color: #FF5A49;
    border-color: #FF5A49;
}

.btn-danger:hover{
    color: #FF5A49;
    border-color: #FF5A49;
    background-color: #fff;
    
}
.AppBar{
    background-color: #FF5A49!important;
}
